// __tests__/screens/Dashboard/components/LearningProgress.test.js
import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import LearningProgress from '../../../../src/screens/Dashboard/components/LearningProgress';
import { ThemeContext } from '../../../../src/contexts/ThemeContext';

// Mock du composant Card
jest.mock('../../../../src/components/ui/Card', () => {
  const { View } = require('react-native');
  return ({ children, style, ...props }) => (
    <View style={style} {...props} testID="learning-progress-card">
      {children}
    </View>
  );
});

// Mock des constantes
jest.mock('../../../../src/utils/constants', () => ({
  LANGUAGE_LEVELS: {
    '1': { title: 'Débutant', color: '#10B981', icon: '🌱' },
    '2': { title: 'Élémentaire', color: '#3B82F6', icon: '📚' },
    '3': { title: 'Intermédiaire', color: '#8B5CF6', icon: '🎯' },
    '4': { title: 'Intermédiaire+', color: '#F59E0B', icon: '🚀' },
    '5': { title: 'Avancé', color: '#EF4444', icon: '⭐' },
    '6': { title: 'Expert', color: '#EC4899', icon: '👑' },
    'bonus': { title: 'Bonus', color: '#6366F1', icon: '🎁' }
  }
}));

describe('LearningProgress', () => {
  const mockThemeContext = {
    colors: {
      surface: '#FFFFFF',
      text: '#1F2937',
      textSecondary: '#6B7280'
    }
  };

  const mockLevels = [
    { id: '1', color: '#10B981', progress: 75, isActive: true },
    { id: '2', color: '#3B82F6', progress: 50, isActive: false },
    { id: '3', color: '#8B5CF6', progress: 25, isActive: false },
    { id: 'bonus', color: '#6366F1', progress: 0, isActive: false }
  ];

  const mockOnSelectLevel = jest.fn();
  const mockOnChangeLevelVisual = jest.fn();

  const renderWithTheme = (component, themeValue = mockThemeContext) => {
    return render(
      <ThemeContext.Provider value={themeValue}>
        {component}
      </ThemeContext.Provider>
    );
  };

  beforeEach(() => {
    jest.clearAllMocks();
  });

  describe('Rendu de base', () => {
    it('devrait afficher le titre de section', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
        />
      );

      expect(getByText('🏆 Progression générale')).toBeTruthy();
    });

    it('devrait afficher les informations du niveau courant', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
        />
      );

      expect(getByText('Débutant')).toBeTruthy();
      expect(getByText('Continuez votre apprentissage 🌱')).toBeTruthy();
      expect(getByText('75%')).toBeTruthy();
    });

    it('devrait afficher la barre de progression globale', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="2"
          globalProgress={60}
        />
      );

      expect(getByText('Progression globale')).toBeTruthy();
      expect(getByText('60%')).toBeTruthy();
    });

    it('devrait afficher le bouton d\'exploration', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="3"
          globalProgress={40}
        />
      );

      expect(getByText('Explorer le niveau 3')).toBeTruthy();
    });
  });

  describe('Affichage des niveaux', () => {
    it('devrait afficher tous les cercles de niveau', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
        />
      );

      expect(getByText('1')).toBeTruthy();
      expect(getByText('2')).toBeTruthy();
      expect(getByText('3')).toBeTruthy();
      expect(getByText('B')).toBeTruthy(); // bonus affiché comme "B"
    });

    it('devrait utiliser les niveaux par défaut si aucun niveau fourni', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={[]}
          currentLevel="2"
          globalProgress={50}
        />
      );

      // Devrait utiliser LANGUAGE_LEVELS par défaut
      expect(getByText('1')).toBeTruthy();
      expect(getByText('2')).toBeTruthy();
      expect(getByText('3')).toBeTruthy();
    });

    it('devrait marquer le niveau courant comme actif', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="2"
          globalProgress={50}
        />
      );

      // Le niveau 2 devrait être affiché comme actif
      expect(getByText('Élémentaire')).toBeTruthy(); // Titre du niveau 2
    });
  });

  describe('Gestion des clics', () => {
    it('devrait appeler onChangeLevelVisual lors du clic sur un cercle de niveau', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
          onChangeLevelVisual={mockOnChangeLevelVisual}
        />
      );

      const level2Circle = getByText('2');
      fireEvent.press(level2Circle);

      expect(mockOnChangeLevelVisual).toHaveBeenCalledWith('2');
    });

    it('devrait appeler onSelectLevel lors du clic sur le bouton explorer', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="3"
          globalProgress={40}
          onSelectLevel={mockOnSelectLevel}
        />
      );

      const exploreButton = getByText('Explorer le niveau 3');
      fireEvent.press(exploreButton);

      expect(mockOnSelectLevel).toHaveBeenCalledWith('3');
    });

    it('ne devrait pas planter si les callbacks ne sont pas fournis', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
        />
      );

      const level2Circle = getByText('2');
      const exploreButton = getByText('Explorer le niveau 1');

      expect(() => {
        fireEvent.press(level2Circle);
        fireEvent.press(exploreButton);
      }).not.toThrow();
    });
  });

  describe('Gestion du niveau bonus', () => {
    it('devrait afficher "B" pour le niveau bonus', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="bonus"
          globalProgress={10}
        />
      );

      expect(getByText('B')).toBeTruthy();
      expect(getByText('Explorer le niveau B')).toBeTruthy();
    });

    it('devrait afficher les informations du niveau bonus', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="bonus"
          globalProgress={5}
        />
      );

      expect(getByText('Bonus')).toBeTruthy();
      expect(getByText('Continuez votre apprentissage 🎁')).toBeTruthy();
    });
  });

  describe('Couleurs et thème', () => {
    it('devrait utiliser la couleur primaire personnalisée', () => {
      const customColor = '#FF5722';
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
          primaryColor={customColor}
        />
      );

      // Vérifier que le composant se rend sans erreur avec la couleur personnalisée
      expect(getByText('75%')).toBeTruthy();
      expect(getByText('Explorer le niveau 1')).toBeTruthy();
    });

    it('devrait utiliser la couleur par défaut si aucune couleur n\'est spécifiée', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="2"
          globalProgress={50}
        />
      );

      expect(getByText('50%')).toBeTruthy();
      expect(getByText('Explorer le niveau 2')).toBeTruthy();
    });

    it('devrait fonctionner sans contexte de thème', () => {
      const { getByText } = render(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
        />
      );

      expect(getByText('🏆 Progression générale')).toBeTruthy();
      expect(getByText('Débutant')).toBeTruthy();
      expect(getByText('75%')).toBeTruthy();
    });

    it('devrait utiliser les couleurs par défaut si le contexte de thème est incomplet', () => {
      const incompleteTheme = {
        colors: {
          surface: '#FFFFFF'
          // text et textSecondary manquants
        }
      };

      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
        />,
        incompleteTheme
      );

      expect(getByText('D��butant')).toBeTruthy();
      expect(getByText('75%')).toBeTruthy();
    });
  });

  describe('Gestion des props par défaut', () => {
    it('devrait utiliser les valeurs par défaut', () => {
      const { getByText } = renderWithTheme(<LearningProgress />);

      expect(getByText('🏆 Progression générale')).toBeTruthy();
      expect(getByText('Débutant')).toBeTruthy(); // Niveau 1 par défaut
      expect(getByText('0%')).toBeTruthy(); // Progression 0 par défaut
      expect(getByText('Explorer le niveau 1')).toBeTruthy();
    });
  });

  describe('Gestion des cas limites', () => {
    it('devrait gérer une progression supérieure à 100%', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={150}
        />
      );

      expect(getByText('150%')).toBeTruthy();
    });

    it('devrait gérer une progression négative', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={-10}
        />
      );

      expect(getByText('-10%')).toBeTruthy();
    });

    it('devrait gérer un niveau courant inexistant', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="invalid"
          globalProgress={25}
        />
      );

      // Devrait fallback au niveau 1
      expect(getByText('Débutant')).toBeTruthy();
      expect(getByText('Explorer le niveau invalid')).toBeTruthy();
    });

    it('devrait gérer des niveaux avec des couleurs manquantes', () => {
      const levelsWithoutColors = [
        { id: '1', progress: 50 },
        { id: '2', progress: 25 }
      ];

      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={levelsWithoutColors}
          currentLevel="1"
          globalProgress={50}
        />
      );

      expect(getByText('1')).toBeTruthy();
      expect(getByText('2')).toBeTruthy();
    });
  });

  describe('Performance et optimisation', () => {
    it('ne devrait pas recréer les handlers à chaque rendu', () => {
      const { rerender } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
          onChangeLevelVisual={mockOnChangeLevelVisual}
        />
      );

      // Premier clic
      const level2Circle = getByText('2');
      fireEvent.press(level2Circle);
      expect(mockOnChangeLevelVisual).toHaveBeenCalledTimes(1);

      // Rerender avec les mêmes props
      rerender(
        <ThemeContext.Provider value={mockThemeContext}>
          <LearningProgress 
            levels={mockLevels}
            currentLevel="1"
            globalProgress={75}
            onChangeLevelVisual={mockOnChangeLevelVisual}
          />
        </ThemeContext.Provider>
      );

      // Deuxième clic - devrait toujours fonctionner
      fireEvent.press(level2Circle);
      expect(mockOnChangeLevelVisual).toHaveBeenCalledTimes(2);
    });
  });

  describe('Accessibilité', () => {
    it('devrait avoir les testIDs appropriés', () => {
      const { getByTestId } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
        />
      );

      expect(getByTestId('learning-progress-card')).toBeTruthy();
    });

    it('devrait permettre l\'interaction avec tous les éléments cliquables', () => {
      const { getByText } = renderWithTheme(
        <LearningProgress 
          levels={mockLevels}
          currentLevel="1"
          globalProgress={75}
          onChangeLevelVisual={mockOnChangeLevelVisual}
          onSelectLevel={mockOnSelectLevel}
        />
      );

      // Tous les cercles de niveau devraient être cliquables
      mockLevels.forEach(level => {
        const levelText = level.id === 'bonus' ? 'B' : level.id;
        const levelCircle = getByText(levelText);
        fireEvent.press(levelCircle);
      });

      // Le bouton explorer devrait être cliquable
      const exploreButton = getByText('Explorer le niveau 1');
      fireEvent.press(exploreButton);

      expect(mockOnChangeLevelVisual).toHaveBeenCalledTimes(4); // 4 niveaux
      expect(mockOnSelectLevel).toHaveBeenCalledTimes(1);
    });
  });
});